



.topHeader {
    background: white;
    padding-top:30px;
    height: 400px;

}

.topHeaderContent {
    width: 980px;
    margin: 0 auto;
    display:flex;
    justify-content: center;
    flex-direction: row;
}

.topHeaderLeft {
    width:60%;
}

.topHeaderRight {
    width:40%;

}

.topHeaderRight img {
}

.topHeader div:nth-child(1) {
    font-weight: bold;
    font-size: 50px;
}

.headerSubTitle  {
    margin-top: 25px;
    border-left: 5px solid #feb858;
    font-size: 20px;
    font-weight: 300;
    padding-left: 10px;
}

.headerSubTitle span {
    color: #feb858;
    font-weight: bold;
}

body {
    background-color: #feb858;
}

* {
    margin: 0;
    padding:0;
}

.wrap {
    width: 980px;
    margin: 0 auto;
    margin-top: 50px;

}

table {
    border-collapse: separate;
    border-spacing: 0 10px;
    margin-top: -10px; /* correct offset on first border spacing if desired */
}

 tr {
     box-shadow: 5px 6px 12px 0px rgba(0, 0, 0, 0.32);
 }

td {
    border: solid 1px white;
    padding: 10px;
}
td:first-child {
    border-left-style: solid;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}
td:last-child {
    border-right-style: solid;
    border-bottom-right-radius: 10px;
    border-top-right-radius: 10px;
}

.buttonWrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}

.buttonLoadMore{
    background: white;
    padding: 14px;
    border:none;
    border-radius: 20px;
    color: #feb858;
    font-weight: bolder;
    font-size: 16px;
    margin-bottom: 40px;
}

.buttonLoadMore:hover{
    background: #fff6eb;
    padding: 15px;
    border:none;
    border-radius: 20px;
    color: #feb858;
    font-weight: bolder;
    font-size: 16px;
}

html {
    position: relative;
    min-height: 100%;
}

body {
    margin: 0 0 100px;
    /* bottom = footer height */
}

footer {

    background-color: #0d132a;
    color: white;
    position: absolute;
    left: 0;
    bottom: 0;
    height: 60px;
    width: 100%;
    overflow: hidden;
}

footer div {
    width: 980px;
    margin: 0 auto;
    display: flex;
    height: 70px;
    align-items: center;
    justify-content: left;
}

footer img {
    margin-right: 20px;
    width:30px;
}

.footerText p {
    margin-left: auto;
}

.pretitle {
    font-weight: bold;
}

.wrap p:nth-child(2), p:nth-child(3) {
    color:white;
    font-size: 40px;
    font-weight: bold;
}

.wrap p:nth-child(3) span {
    color: black;
    box-shadow: inset 0 0px 0 white, inset 0 -15px 0 #fed092;
}

.headerImages {
    margin-top: 20px;
    display:flex;
    flex-direction: row;
    align-items: center;
}

.headerImages img {
    -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
    margin-right: 30px;
    width: 100px;
}


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
